.popupbox-trigger {
  position: absolute;
  transition: all .2s;
  color: #fff;
  text-shadow: 0 -1px 1px #000;
  background-color: MDcolor(blue-grey, 900);
  box-shadow: 0 1px 1px rgba(black, .4);
  font-family: $b-fontFamily-heading, cursive, sans-serif;
  font-size: 24px;
  padding: 12px 16px 16px;
  height: 20px;
  width: 200px;
  top: 50%;
  left: 50%;
  margin: -10px 0 0 -116px;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
  display: block;

  &:hover {
    box-shadow: 2px 3px 5px rgba(black, .2);
    margin-top: -12px;
  }
}

.quotes-from {
  color: MDcolor(grey, 500);
  font-weight: bold;
  float: right;
}

.quotes {
  color: MDcolor(grey, 800);
  font-style: italic;
  line-height: 1em;
}

.demo-component {
  position: relative;
  height: 100px;
}

.demo-source pre.prettyprint {
  max-width: 70%;
  margin: auto;
  border: none;
  box-shadow: 0 1px 1px rgba(black, .3);
}

.demo-componentDescribe {
  display: block;
  text-align: center;
}

h2 {
  text-align: center;
  margin-top: 40px;
  font-size: 26px;
  color: MDcolor(light-blue, 700);
}



