.header {
  padding: 70px 60px 50px;
  background-color: MDcolor(light-blue, 600);
  border-top: 10px MDcolor(light-blue, 900) solid;
  letter-spacing: .3px;
  color: white;
  text-align: center;
  border-bottom: 1px MDcolor(light-blue, 700) solid;
  font-family: $b-fontFamily-heading, cursive, sans-serif;

  a {
    transition: all .3s;
    color: MDcolor(light-blue, 100);
    border-bottom: 1px MDcolor(light-blue, 100) solid;
    text-decoration: none;
    font-weight: bold;

    &:hover {
      color: white;
      color: MDcolor(light-blue, 50);
      text-decoration: none;
      border-bottom: 1px white solid;
    }
  }
}

.header-title {
  text-shadow: 0 -1px 1px rgba(black, .3);
  margin: 0;
}

.header-describe {
  color: MDcolor(light-blue, 100);
}



