.options {
  width: 60%;
  margin: 40px auto;
  font-family: $b-fontFamily-heading, cursive, sans-serif;

  th.options-type {
    text-align: center;
    background-color: MDcolor(blue-grey, 100);
  }

  th, td {
    padding: 10px 15px;
  }

  th {
    text-align: left;
    background-color: MDcolor(blue-grey, 200);
    &:last-child {
      text-align: center;
    }
  }
}



