.footer {
  background-color: MDcolor(light-blue, 900);
  padding: 30px 60px 80px;
  border-top: MDcolor(light-blue, 600);
  text-align: center;
  font-family: $b-fontFamily-heading, cursive, sans-serif;
}

.license {
  display: block;
  color: white;
  font-size: 18px;
}

.license-title {
  font-weight: bold;
}

.made-with-love {
  color: #ccc;
  padding: 10px 0;
  margin-bottom: 10px;

  a {
    color: #eee;
    margin-left: 5px;
    color: white;
    font-weight: bold;
    text-decoration: none;
    border-bottom: 1px white solid;
  }
}

.github-button a {
  padding: 10px 13px;
  color: white;
  text-shadow: 0 -1px 1px rgba(black, .3);
  background-color: MDcolor(light-blue, 600);
  text-decoration: none;
  border-radius: 5px;
  box-shadow: 0 1px 1px rgba(black, .5);
}


